@import "static/bos/scss/breakpoint";
@import "static/portal/scss/var";
/* 다국어를 제외한 모든 사이트 공용 모바일 네비게이션 */
.familySetOpen{
	@include breakpoint($lg){display:none;}
	&:before {content: '\f00a';display:inline;}
	position: absolute;
	right: 0;
	top: 71px;
	font-family: 'FontAwesome';
	font-size: 20px;
	width: 48px;
	height: 30px;
	z-index:601;
}
.familySetClose{
	display:none; 
}
.gnbClose{display:none;}
#allmenu{ 
	display:none;
	@include breakpoint($smd){
		display:block;
		position:absolute;
		left:0;
		top:0;
		padding: 0 0 0 0;
	}
	>button{
		width:30px;
		width:48px;
		height:60px;
		text-align:center;
		background:none;
		font-size:21px;
		padding:0;
		&:after {
			content: '';
			font-family:'FontAwesome';
		}
	}
}
@include breakpoint( $smd ){
	#gnb>ul{display:none;}
	#gnb.mobileOn{
		.gnbClose{
			display:block;
			position:absolute;
			left:320px - 54;
			top:0;
			height:54px;
			width:54px;
			color:#fff;
			background-color:#061d45;
			// border-left:1px solid #061d45;
			&:after {content: '\f00d'; font-family:'FontAwesome';}
		}
		display:block !important;
		position:absolute;
		left:0;
		top:0;
		width:100%;
		width:320px;
		border-top:none;
		padding:54px 0 0 0;
		margin:0;
		z-index:600;
		&:before {
			content: '';
			position:fixed;
			left:0;
			top:0;
			right:0;
			bottom:0;
			background-color:rgba(0,0,0,0.5);
			z-index:-1;
		}
		&:after {
			content: '';
			background:#fff url(/static/portal/img/common/logo.png) 33px center no-repeat;
			background-size:auto 54px;
			position:absolute;left:0;top:0;right:54px;
			height:54px;
		}
		li{position:relative}
		>ul{
			display:block;
			border:1px solid $bdcolor;
			border-top:2px solid #061d45;
			background-color:#fff;
			>li{
				position:relative;
				font-size:22px;
				>a{
					display:block;
					line-height:1;
					color:#061d45;
					padding:17px 0 17px 55px;
					border-bottom:1px solid $bdcolor;
					background:#fff url(/static/portal/img/common/bu1.png) 24px center no-repeat;
					font-family:'Noto Sans';
					font-weight:500;
					// background-size:19px;
				}
				&.active>a{
					// background-color:#f5fcff;
				}
				// 1뎁스
				&.active > div .depth2 > ul,
				// 2뎁스
				&.active > div .depth2 > ul > li.active > div > ul,
				&.hover .depth2>ul{display:block;}
				&.active>div:after{transform:rotate(0);}
				&.nosub>div{display:none;}
				>div{
					&:after,
					&:before{
						background-color:#061d45;
						content: '';
						width:18px;height:2px;
						position:absolute;right:17px;top:25px;
						transition: all .25s ease-out;
					}
					&:after{transform:rotate(90deg);}
					.img{display:none;}
					.depth2{
						>ul{
							display:none;
							// background-color:#f1f1f1;
							>li{
								border-bottom:1px solid $bdcolor;
								position:relative;
								>div{
									&:after,
									&:before{
										content: '';
										width:18px;height:2px;background-color:#757575;
										position:absolute;right:17px;
										top:32px;
										transition: all .25s ease-out;
									}
									&:after{transform:rotate(90deg);}
									>ul{display:none;}
								}
								&.active>div:after{transform:rotate(0)}
								&.active>ul{display:block;}
								&.active>a{
									background-color:#688ccf;color:#fff;
									&:before {
										background-color:#fff;										
									}
								}
								>a{
									display:block;
									padding:17px 0 17px 55px;
									font-size:0.909em;

									&:before {
										content: '';
										position:absolute;
										width:6px;height:6px;background-color:#061d45;
										left:31px;
										top:31px;
									}
								}
								
							}
						}
						li.active>div:after{transform:rotate(0)}
						li.active>div>ul{display:block;}
						li.active>div:after{transform:rotate(0)}

						.depth3{
							background-color:#fff;
							margin:0 36px;
							&:after,
							&:before{
								content: '';
								width:18px;height:2px;background-color:#757575;
								position:absolute;right:17px;
								top:calc(50% - 0);
								transition: all .25s ease-out;
							}
							&:after{transform:rotate(90deg);}
							ul{
								
								// border:1px solid $bdcolor;
								padding:13px 0;
								// margin:10px 0;
								display:none;
							}
							li{}
							a{
								display:block;
								// background: url(/static/portal/img/common/mNavBu4.png) 22px center no-repeat;
								background-size:4px;
								border:none;
								padding:2px 0 2px 0;
								font-size:calc(1em * 0.8);
								&:before {
									content: '- ';
									
								}
							}
						}
					}

				}
			}
		}
	}
	
	#globalWarp .familySet.mobileOn{
		display:block !important;
		position:absolute;
		right:0;
		top:66px;
		width:100%;
		width:320px;
		border-top:none;
		padding:54px 0 0 0;
		margin:0;
		z-index:602;
		.familySetClose{display:block;}
		&:before {
			content: '';
			position:fixed;
			left:0;
			top:0;
			right:0;
			bottom:0;
			background-color:rgba(0,0,0,0.5);
			z-index:-1;
		}
		&:after {
			content: '';
			background:#fff url(/static/portal/img/common/logo.png) 10px 10px no-repeat;
			background-size:auto 34px;
			position:absolute;left:0;top:0;right:54px;
			height:54px;
		}

		>ul{
			border:1px solid $bdcolor;
			border-top:2px solid #061d45;
			>li{
				position:relative;
				float:none;
				
				>a{
					display:block;
					line-height:1;
					color:#333333;
					padding:17px 0 17px 45px;
					border-bottom:1px solid $bdcolor;
					background:#fff url(/static/mayor/img/main/mNavBu1.png) 18px center no-repeat;
					background-size:19px;
				}
				&.on>a{
					background-color:#f5fcff;
				}
				&.on>div>ul,
				// &:hover>div>ul,
				&.hover>div>ul{display:block;}
				&.on>div:after{transform:rotate(0);}
				>div{
					&:after,
					&:before{
						content: '';
						width:18px;height:2px;background-color:#061d45;
						position:absolute;right:17px;top:25px;
						transition: all .25s ease-out;
						z-index:10002;
					}
					&:after{transform:rotate(90deg);}
					>ul{
						display:none;
						background-color:#f1f1f1;
						>li{
							border-bottom:1px solid $bdcolor;
							a{
								display:block;
								padding:17px 0 17px 45px;
								background: url(/static/mayor/img/main/mNavBu2.png) 22px center no-repeat;
								background-size:13px;
							}
						}
					}
				}
			}
		}
	}
	// 모바일 메뉴 활성화
	#gnb.mobileOn > ul > li.active{
		 > div .dinfoSet > ul{
		 	// >li.active>a{color:$sitecolor; text-decoration:underline;}
			> li.active > div > ul{
				display:block;
				>li.active{
					>div{
						
						>ul{
							display:block;
							>li.active{
								a{color:$sitecolor; text-decoration:underline;}
							}
						}
					}
				}
			}
		}
	}
}